import React, { useState, useEffect } from 'react'
import { Container, Grid, AppBar, Grow, Typography } from '@material-ui/core'
import { useDispatch } from 'react-redux'

import memory from './images/memory.png'
import Form from './components/Form/Form.js'
import Posts from './components/Posts/Posts.js'
import useStyles from './styles.js'
import { getPosts } from './actions/posts'

const App = () => {

    const [currentId, setCurrentId] = useState(null)
    const classes = useStyles();
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(getPosts())
    }, [currentId, dispatch])

    return (
        <Container maxWidth="lg">
          <AppBar className={classes.appBar} position="static" color="inherit">
            <Typography className={classes.heading} variant="h4" align="center">Memories</Typography>
            <img className={classes.image} src={memory} alt="icon" height="40" width="40" />
          </AppBar>
          <Grow in>
            <Container>
              <Grid  className={classes.mainContainer} container justify="space-between" alignItems="stretch" spacing={3}>
                <Grid item xs={12} sm={7}>
                  <Posts setCurrentId={setCurrentId} />
                </Grid>
                <Grid item xs={12} sm={4}>
                  <Form currentId={currentId} setCurrentId={setCurrentId} />
                </Grid>
              </Grid>
            </Container>
          </Grow>
        </Container>
    );
}

export default App
